body {
  font-size: 18px;
  background: #FFF;
  line-height: 1.4;
}

.block {
  display: flex;
}

.button {
  font-family: "Montserrat", Helvetica, sans-serif;
  font-size: 18px;
  color: #fff;
  background-color: #1d354e;
  text-decoration: none;
  padding: 22px 68px;
  border-radius: 50px;
}

.button:hover {
  background-color: rgba(29, 53, 78, 0.7);
}

.button:active {
  background-color: rgba(29, 53, 78, 0.85);
}

.button--border-middle {
  border-radius: 20px;
  background-color: #fff;
  color: #1d354e;
  padding: 10px 25px;
  font-size: 16px;
}

.button--border-middle:hover {
  background: rgba(255, 255, 255, 0.7);
}

.button--border-middle:active {
  background: rgba(255, 255, 255, 0.85);
}

.button--rectangle {
  box-sizing: border-box;
  padding: 0;
  border: 8px #223e5c solid;
  border-radius: 8px;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 30px;
}

.button--rectangle:hover {
  background-color: #00bdca;
  border: none;
}

.button--rectangle:active {
  background-color: rgba(0, 189, 202, 0.7);
}

.header {
  font-family: "Montserrat", Helvetica, sans-serif;
  color: #fff;
  background-color: #00bdca;
  padding: 64px 0 115px;
}

.header__inner {
  font-size: 0;
  margin: auto;
  max-width: 996px;
  display: flex;
  justify-content: space-between;
}

.header__menu {
  display: flex;
  align-items: center;
}

.header__login {
  margin: 0 8px;
  font-weight: 600;
  font-size: 16px;
}

.logo {
  font-family: "Montserrat", Helvetica, sans-serif;
  display: flex;
}

.logo__image {
  display: flex;
  align-items: center;
}

.logo__image .icon {
  width: 43px;
  height: 44px;
  fill: #fff;
}

.logo__text {
  margin-left: 12px;
}

.logo__title {
  font-weight: 600;
  font-size: 24px;
}

.logo__title--cyan {
  color: #56ecf6;
}

.logo__description {
  line-height: 0.5;
  font-weight: 400;
  font-size: 13px;
}

.nav {
  font-family: "Montserrat", Helvetica, sans-serif;
}

.nav__list {
  display: flex;
}

.nav__item {
  margin: 0 17px;
}

.nav__item:last-child {
  margin-right: 25px;
}

.nav__link {
  text-decoration: none;
  color: #fff;
  font-weight: 600;
  font-size: 16px;
}

.nav__link:hover {
  text-decoration: underline;
}

.nav__link:active {
  color: rgba(255, 255, 255, 0.7);
}

.lang {
  font-family: "Montserrat", Helvetica, sans-serif;
  position: relative;
}

.lang__current {
  padding: 10px 24px;
  font-size: 16px;
  color: transparent;
}

.lang__list {
  padding: 10px 24px;
  background-color: #fff;
  border-radius: 20px;
  left: 0;
  top: 0;
  position: absolute;
}

.lang__list:hover .lang__item--hidden {
  display: block;
}

.lang__item {
  font-size: 16px;
}

.lang__item:hover {
  text-decoration: underline;
}

.lang__item--hidden {
  display: none;
}

.lang__link {
  text-decoration: none;
  color: #1d354e;
  font-weight: 600;
  font-size: 16px;
}

.hero {
  padding-bottom: 100px;
  position: relative;
  height: 1300px;
  background-color: #1d354e;
}

.hero__background {
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
}

.hero__background:before {
  content: "";
  bottom: 46.5%;
  position: absolute;
  width: 100%;
  height: 50000%;
  transform: skewY(30deg);
  background-color: #00bdca;
}

.hero__box {
  position: absolute;
  left: 112px;
  top: 174px;
}

.hero__box .icon {
  width: 116px;
  height: 135px;
}

.hero__box-blurred {
  position: absolute;
  right: 275px;
  top: 335px;
  z-index: 1;
}

.hero__box-blurred .icon {
  filter: blur(15px);
  width: 234px;
  height: 258px;
}

.hero__inner {
  max-width: 1170px;
  margin: auto;
}

.hero__text {
  display: flex;
  max-width: 996px;
  margin: auto;
  position: relative;
  height: 578px;
}

.hero__image {
  position: absolute;
  height: 820px;
  top: -39px;
  left: -175px;
  z-index: 1;
}

.hero__title-wrapper {
  z-index: 1;
  font-family: "Montserrat", Helvetica, sans-serif;
  color: #fff;
  margin-left: auto;
  text-align: center;
  width: 62%;
}

.hero__title {
  font-weight: 600;
  font-size: 72px;
  margin-top: 15px;
  line-height: 1.1;
}

.hero__description {
  margin-top: 18px;
  font-weight: 600;
  font-size: 30px;
}

.hero__button {
  display: inline-block;
  margin-top: 28px;
}

.hero__video {
  position: relative;
  display: flex;
  align-items: center;
  height: 722px;
  background: #fff;
  border-radius: 10px;
}

.hero__video-container {
  display: flex;
  width: 1080px;
  height: 624px;
  margin: auto;
  background: #000;
  border-radius: 10px;
}

.hero__video-play {
  width: 120px;
  height: 120px;
  margin: auto;
  background: url("../../img/play-button.svg") no-repeat center;
  background-size: cover;
}

.hero__video-play:hover {
  opacity: 0.7;
}

.hero__video-play:active {
  opacity: 0.85;
}

.graph {
  position: relative;
  background-color: #1d354e;
  padding-top: 147px;
}

.graph__box {
  position: absolute;
  right: 158px;
  top: 75px;
}

.graph__box .icon {
  width: 96px;
  height: 112px;
}

.graph__big-box {
  position: absolute;
  left: -70px;
  top: 200px;
}

.graph__big-box .icon {
  width: 333px;
  height: 333px;
}

.graph__header {
  font-family: "Montserrat", Helvetica, sans-serif;
  color: #fff;
  max-width: 996px;
  margin: auto;
}

.graph__title {
  font-weight: 600;
  font-size: 60px;
  line-height: 72px;
}

.graph__description {
  font-weight: 600;
  font-size: 24px;
  line-height: 48px;
}

.graph__buttons {
  margin-top: 32px;
  margin-right: 25px;
  display: flex;
  justify-content: space-between;
}

.graph__button {
  width: 137px;
  height: 83px;
}

.graph__content {
  width: 100%;
  height: 564px;
  margin-top: 42px;
  background-color: #223e5c;
}

.about {
  font-family: "Montserrat", Helvetica, sans-serif;
  overflow: hidden;
  color: #1d354e;
  position: relative;
  background-color: #00bdca;
  padding-top: 145px;
}

.about:before {
  content: "";
  top: auto;
  bottom: 41%;
  position: absolute;
  width: 100%;
  height: 50000%;
  transform: skewY(30deg);
  background-color: #1d354e;
}

.about__inner {
  background-color: #fff;
  border-radius: 18px;
  position: relative;
  max-width: 1170px;
  min-height: 1160px;
  box-sizing: border-box;
  margin: 0 auto 44px;
  padding: 74px 50px 105px 95px;
}

.about__title {
  font-size: 60px;
  font-weight: 600;
  color: #1d354e;
}

.about__list-wrapper {
  display: flex;
  flex-wrap: wrap;
}

.about__list {
  width: 50%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.about__item {
  margin-top: 20px;
  padding-right: 60px;
  display: flex;
  width: 100%;
}

.about__item .icon {
  width: 50px;
  height: 50px;
  fill: #00bdca;
  flex-shrink: 0;
  margin-right: 30px;
}

.about__text {
  font-weight: 300;
  line-height: 2;
}

.news {
  color: #fff;
  background-color: #00bdca;
  font-family: "Montserrat", Helvetica, sans-serif;
}

.news__inner {
  position: relative;
  flex-direction: column;
  max-width: 973px;
  margin: auto;
}

.news__box-blurred {
  position: absolute;
  right: -340px;
  top: 205px;
  z-index: 1;
}

.news__box-blurred .icon {
  filter: blur(25px);
  width: 416px;
  height: 446px;
}

.news__title {
  padding-top: 64px;
  text-transform: capitalize;
  font-weight: 600;
  font-size: 60px;
}

.news__list {
  display: flex;
  justify-content: space-between;
  padding-top: 28px;
}

.news__item {
  width: 31%;
}

.news__image-wrapper {
  width: 300px;
  height: 300px;
  background-color: #223e5c;
  border-radius: 8px;
}

.news__item-title {
  margin-top: 14px;
  line-height: 1.25;
  font-weight: 600;
  font-size: 30px;
}

.news__description {
  margin-top: 12px;
  line-height: 1.7;
  font-weight: 400;
  font-size: 14px;
}

.news__button {
  display: block;
  margin: 28px auto 0;
  padding: 22px 110px;
}
