.news {
	color: $white;
	background-color: $cyan;
	font-family: $montserrat;

	&__inner {
		position: relative;
		flex-direction: column;
		max-width: 973px;
		margin: auto; }

	&__box-blurred {
		position: absolute;
		right: -340px;
		top: 205px;
		z-index: 1;
		.icon {
			filter: blur(25px);
			width: 416px;
			height: 446px; } }

	&__title {
		padding-top: 64px;
		text-transform: capitalize;
		font-weight: 600;
		font-size: 60px; }

	&__list {
		display: flex;
		justify-content: space-between;
		padding-top: 28px; }

	&__item {
		width: 31%; }

	&__image-wrapper {
		width: 300px;
		height: 300px;
		background-color: $dark-blue;
		border-radius: 8px; }

	&__image {}

	&__item-title {
		margin-top: 14px;
		line-height: 1.25;
		font-weight: 600;
		font-size: 30px; }

	&__description {
		margin-top: 12px;
		line-height: 1.7;
		font-weight: 400;
		font-size: 14px; }

	&__button {
		display: block;
		margin: 28px auto 0;
		padding: 22px 110px; } }

